.navbar {
  background-color: #022b3a;
  color: white;
  padding: 15px;
}

a {
  color: white;
}

a:hover {
  text-decoration: underline;
  color: #1f7a8c;
}

.navbar-brand {
  font-size: 35px;
}

.nav-item {
  font-size: 25px;
}

.jumbotron {
  height: 250px;
  background-color: #e1e5f2;
  color: #022b3a;
  font-size: larger;
}

input {
  width: 50%;
}

.form-group button {
  background-color: #e1e5f2;
  color: #022b3a;
}

.form-group button:hover {
  background-color: #022b3a;
  color: #e1e5f2;
}

.resultsData {
  background-color: #022b3a;
  color: #1f7a8c;
  padding: 20px 50px 20px 50px;
}

.resultsData h3 {
  color: white;
}

body {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  margin: 0;
}

.App {
  text-align: center;
}

